import React from "react";
import { Col, Layout, Row, Divider, Avatar, Button, Card, Input, Skeleton, List } from "antd";
import Banner from "../../components/banner/Banner";
import { Wrapper, WrapperList } from "./Styles";
import ContacForm from "../../components/form-hotel/ContactForm";
import { isMobile } from "react-device-detect";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";
import LayoutCanter from "../../layouts/layout_center";
import SearchField from "../../components/search-field/SearchField";


const data = [
    {
        id: 1,
        title: "0180003456",
        icon: "/images/Icon feather-headphones.png",

    },
    {
        id: 2,
        title: "hotelestour@gmail.com",
        icon: "/images/Icon feather-mail.png",

    }
    ,
    {
        id: 3,
        title: "3145678987",
        icon: "/images/Icon awesome-whatsapp.png",

    }
    ,
    {
        id: 4,
        title: "Lun - Vie: 8:00 am - 6:00 pm Sáb - Dom: 9:00 am - 12:00pm",
        icon: "/images/Icon feather-calendar.png",


    }
];
const ListContac = ({ props }) => {

    return (
        <WrapperList>

            <List
                itemLayout="horizontal"
                dataSource={data}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            avatar={<Avatar className="container-avatar" src={item.icon} />}
                            title={<a className="container-descriptions">{item.title}</a>}
                        //description={<a>{item.description}</a>}
                        />
                    </List.Item>
                )}
            />

        </WrapperList >
    );
};
const Contact = ({ t, props }) => {
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    })

    return (
        <Wrapper>
            <Banner

                dataSource={[{
                    title: t("contact.bannerDescription"),
                    description: t("contact.bannerTitle"),
                    image_path: "/images/Contáctanos.jpg",
                    thubmnail: "/images/banner-min.png",

                }]} />
            <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
                <Col className="section-container-search" xl={10} lg={16} md={16} xs={22} >
                    <SearchField
                        placeholder={t("search.Text.Input")}
                        reference="packs"
                        open={false}
                        source={`name_${locale}`}
                    />
                </Col>
            </Row>
            <Row type="flex" justify="center">
                <Col span={20}>
                    <Row className="section section-container" justify="space-between" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
                        <Col className="container-section" xl={12} lg={24} md={24} xs={24}>

                            <div className="section-title-form">
                                <h2>{t("contact.labelText.Msj")}</h2>
                            </div>
                            <div className="section-form">
                                <ContacForm />
                            </div>
                        </Col>
                        <Col className="container-list" xl={12} lg={24} md={24} xs={24}>
                            <div className="section-title-contact">
                                <h2>{t("contact.labelText.support")}</h2>
                            </div>
                            <div className="list">
                                <ListContac />
                            </div>
                        </Col>

                    </Row>
                </Col>
            </Row>
        </Wrapper>
    );
}
export default translate()(Contact);

