import styled from 'styled-components';
export const WrapperItem = styled.div`
  
`;
export const WrapperCity = styled.div`
    & .buttons-container{
        margin-right: 18px;
    }
    .ant-btn > .anticon + span, .ant-btn > span + .anticon {
        margin-left: -10px;
        width: 101px;
      
    }
  
    
    & .location-container i.anticon.anticon-environment {
        margin-right: 0px;
        margin-bottom: 4px;
        font-size: 20px;
    }
    
    & .location-container i.anticon.anticon-arrow-right {
        font-weight: bold;
        font-size: 20px;
        height: 40px;
        width: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background-color: var(--color-primary);
        color:var(--color-white);
    }
    & .location-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    & .location-container .ant-icon{
        font-size:20px!important;
        color:var(--color-primary)!important;   
    }
    & .location-menu {
        color: var(--color-primary)!important;
    }
    & .location-container button,
    .location-container .icon-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
      

    ${({ configuration }) => {
        return `
            & .location-container i.anticon.anticon-arrow-right{
                background-color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .anticon-search{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
        `;
    }}
`;
export const Wrapper = styled.div`
    
    box-shadow: 0px 3px 50px #00000029!important;
    background-color:var(--white)!important;

    display: flex;
    justify-content:space-between;
    align-items:center;
    transform: translateY(-50px);

   /*  & .ant-menu-vertical > .ant-menu-item{
        height: 23px !important;
        line-height: 14px!important;
        display: flex;
        justify-content: flex-start;
    }  */
    & .search-item {
        padding-top: 5px!important;
        margin-left: 16px!important;
    }
    & .ant-select-auto-complete.ant-select-lg .ant-select-selection__rendered
    {
        line-height: 80px!important;
    }
    & .ant-select-selection__placeholder, .ant-select-search__field__placeholder,
    .ant-select-auto-complete.ant-select-lg .ant-input
    {
        height: 80px;
        font-size:18px!important;
        margin-left: 30px!important;
        padding-left: 10px!important;
    }
    & .ant-select-auto-complete.ant-select .ant-input{
        border-width: 0px!important;
    }
    & .ant-select-search--inline .ant-select-search__field {
        background: transparent!important;
        border-width: 0!important;
        border-radius: 0px!important;
    }
    & .anticon-search{
        font-size:20px!important;
        color:var(--color-primary)!important;
    }
    & .ant-input:focus, 
    .ant-input:hover {
        border-color: transparent!important;
        border-right-width: 0px !important;
        box-shadow:none!important;
    }
    .ant-select-combobox .ant-select-search__field {
        position: relative;
        z-index: 1;
        width: 85%!important;
        height: 100%;
    }
    ${({ configuration }) => {
        return `
            & .anticon-search{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .location-menu {
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
            & .ant-menu-item:hover, .ant-menu-item-active, .ant-menu:not(.ant-menu-inline) .ant-menu-submenu-open, .ant-menu-submenu-active, .ant-menu-submenu-title:hover {{
                color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
            }
        `;
    }}
`