import styled from 'styled-components';

export const Wrapper = styled.div`

& .container-form{
    display: contents;
}
${({ configuration }) => {
    return `
    & .ant-btn {
        background:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
     }
    `;
}}
`;
export const WrapperForm = styled.div`
& .head-title h1 {
    font-size: 28px;
    font-weight: 600;
    letter-spacing: 3.4px;
    color: #000000;
    text-transform: uppercase;
    opacity: 1;
}
& .ant-btn.login-form-button {
    width: 100%!important;
  }
  
  ${({ configuration }) => {
    return `
        & .ant-btn {
            background:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
        }
    
    `;
}}
`;