import React from "react";
import { Form, Icon, Input, Button, Checkbox, Row, Col, List, InputNumber } from 'antd';
import { Wrapper } from "./Styles";
import { SelectField, SimpleForm } from "../com/form/SimpleForm";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";

const ContacForm = ({ t, ...props }) => {
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    })
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })
    return (
        <Wrapper configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var(--color-white)"
        }}>


            <SimpleForm className="container-form"
                textAcceptButton={t("sendButtonText")}
            >
                <Input
                    name=""
                    label={t("formNamePlaceholderText")}
                    placeholder={t("formNamePlaceholderText")}
                />
                <Input
                    name=""
                    label={t("formCityPlaceholderText")}
                    placeholder={t("formCityPlaceholderText")}
                />
                <Input
                    name=""
                    label={t("emailPlaceholder")}
                    placeholder={t("emailPlaceholder")}
                />
                <InputNumber
                    name=""
                    label={t("formPhonePlaceholderText")}
                    placeholder={t("formPhonePlaceholderText")}
                />
                <Input.TextArea
                    flex={1}
                    name=""
                    label={t("formMsjPlaceholderText")}
                    placeholder={t("formMsjPlaceholderText")}
                />
            </SimpleForm>
        </Wrapper>

    );
};
export default translate()(ContacForm);