const HeadLine = styled.div`
  text-align: center;
  & h2 {
    font-size: 1.5rem;
  }
  & img {
    margin-bottom: 1.5rem !important;
  }
`;
const Footer = styled(Row)`
  & .ant-col {
    margin-bottom: 0.5rem !important;
  }
`;

export const SimpleForm = ({
  source = "",
  title,
  header,
  footer,
  children,
  onSubmit,
  style,
  id,
  idKey = "_id",
  layout = "vertical",
  width = "100%",
  textAcceptButton,
  autoSubmit = true,
  owner = false,
  ownerId = "user_id",
  ...props
}) => {
  const [service, setService] = useState();
  const [record, setRecord] = useState(props.record || {});
  const [submitting, setSubmitting] = useState(false);
  const [loading, setLoading] = useState(false);
  const [_id, setID] = useState();
  const [initialValues, setInitialValues] = useState(props.initialValues);

  const save = async payloads => {
    return new Promise((resolve, reject) => {
      setSubmitting(true);
      if (_id) {
        return service
          .patch(_id, payloads)
          .then(({ msg, ...rest }) => {
            message.success(msg || "Record updated successfully");
            resolve(rest);
            setInitialValues();
            setRecord();
            setSubmitting(false);
            if (props.onAfterSubmit) props.onAfterSubmit(rest);
          })
          .catch(err => {
            reject(err);
            setSubmitting(false);
            message.error(err.message);
          });
      }
      service
        .create(payloads)
        .then(response => {
          message.info(response.message || props.message || "Record created successfully");
          resolve(response);
          setInitialValues();
          setRecord();
          setSubmitting(false);
        })
        .catch(err => {
          reject(err);
          message.error(err.message);
          setSubmitting(false);
        });
    });
  };
  const getData = () => {
    console.log("FORM", props.form);

    if (source && id) {
      const service = getService(source);
      service
        .get(id)
        .then(response => {
          try {
            setInitialValues(response);
            setRecord(response);
            console.log("DATA::: ", response);
            if (props.onLoad) props.onLoad(response);
          } catch (error) {
            console.log("ERROR: ", error);
          }
        })
        .catch(err => console.log(err));
    }
  };
  const handleSubmit = async (err, data, form) => {
    if (err) return;
    if (autoSubmit && source) {
      const response = await save(data);
      if (onSubmit) onSubmit(err, response, form);
    } else {
      if (onSubmit) onSubmit(err, data, form);
    }
  };
  useEffect(() => {
    setService(getService(source));
    let { match } = props;
    if (match && !id) {
      let { params } = match;
      if (params) {
        id = params.id;
      }
    }
    setID(id);
    getData();
    return () => {};
  }, [id]);
  useEffect(() => {
    setLoading(props.loading);
  }, [props.loading]);

  if (loading)
    return (
      <Loader>
        <Spin indicator={antIcon} tip="Loading..." />
      </Loader>
    );
  return (
    <Form
      {...props}
      onSubmit={handleSubmit}
      style={{
        ...style
      }}
      autoSubmit={autoSubmit}
      layout={layout}
      formLayout={layout}
      autoSubmit={autoSubmit}
      initialValues={initialValues}
      submitting={submitting}
      textAcceptButton={
        textAcceptButton ? textAcceptButton : _id ? "UPDATE" : "CREATE"
      }
      footer={
        <Footer type="flex" justify="center" align="middle">
          {footer}
        </Footer>
      }
      record={record}
      title={
        title ? (
          <HeadLine
            style={{
              width: "100%"
            }}
          >
            {title}
          </HeadLine>
        ) : (
          header
        )
      }
    >
      <>
        {React.Children.map(children, (child, index) => {
          if (!child) return null;
          return React.cloneElement(child, {
            record
          });
        })}
      </>
    </Form>
  );
};
