import React, { useState, useEffect } from 'react';
import { Wrapper, WrapperCity } from './Styles';
import { translate } from 'react-polyglot';
import { useSelector, useDispatch } from "react-redux";
import { Layout, message, Input, AutoComplete, Row, Col, Tag, Avatar, Divider, Button, Icon, Dropdown, Menu } from "antd";
import { useLayoutEffect } from 'react';
import { getService } from '../../services';
import { Link, navigate } from "@reach/router";
import { isMobile } from "react-device-detect";
import { DEFAULT_IMAGE, URL_S3 } from '../../constants';
import _ from "lodash";
const data = [{

    title: 'Libraries',
    children: [
        {
            title: 'AntDesign',
            count: 10000,
        },
        {
            title: 'AntDesign UI',
            count: 10600,
        },
    ],
}
];
const { Option, OptGroup } = AutoComplete;
const { Search } = Input;
const MenuDropdown = ({ t, id, reference = "cities", source = "name", onLoading, onChange, filterDefaultValues, item, ...props }) => {
    const [dataSource, setDataSource] = useState([]);
    const [loading, setLoading] = useState(true);
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            setLoading(true);
            if (onLoading)
                onLoading(true);
            service.find({
                query: {
                    ...filterDefaultValues
                }
            })
                .then(({ data }) => {
                    setLoading(false);
                    setDataSource(data);
                    if (onLoading)
                        onLoading(false);
                })
                .catch(err => {
                    message.error(err.message)
                    setLoading(false);
                    if (onLoading)
                        onLoading(false);
                });
        }
    };
    const handleOnChange = (id, record) => {
        if (onChange)
            onChange(id, record[source]);
    }

    useEffect(() => {
        getData();
    }, [filterDefaultValues])
    return (<Menu
        loading={loading}
        className="container-menu">
        {dataSource.map((item, index) => {
            return (<Menu.Item
                className="section-menu-item"
                key={item.id}
                onClick={() => handleOnChange(item.id, item)}
            >
                {item.name}
            </Menu.Item>)
        })}
    </Menu>);
};
const DropDownCity = ({ t, onChange, ...props }) => {
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })
    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [city_name, setCityName] = useState();
    const [city_id, setCityId] = useState();
    const handleClick = () => {
        if (city_id)
            navigate(`/city-packs/${city_id}`);
    }
    const handleOnLoad = (loading) => {
        setOpen(!loading);
        setLoading(loading);
    }
    const handleOnChange = (id, name) => {
        setOpen(open => !open);
        setCityName(name);
        setCityId(id);
        if (onChange) onChange(id, name);
    }
    return (<WrapperCity configuration={{
        primary_color: entity && entity.primary_color || "var(--color-primary);",
        secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
        text_color: entity && entity.text_color || "var( --color-gray-dark)"
    }}>
        <div className="location-container">
            <div className="dropdown-container">
                <Dropdown
                    visible={open}
                    className="location-menu"
                    placement="bottomCenter"
                    trigger={['click']}
                    overlay={<MenuDropdown
                        onLoading={handleOnLoad}
                        onChange={handleOnChange}
                        /* filterDefaultValues={filterDefaultValues} */
                        className="menu-menu-container" />}
                >
                    <Button
                        onClick={e => {
                            setOpen(true);
                        }}
                        loading={loading}
                        icon="environment" block size="large" type="link">
                        {city_name ? city_name : t("search-Text-Location")}
                    </Button>
                </Dropdown>
            </div>
            {<div className="buttons-container">
                <Button

                    block
                    type="link"
                    icon={"arrow-right"}
                    onClick={handleClick}
                />
            </div>}
        </div>
    </WrapperCity>
    );
}
const SearchField = ({ t, source, reference, placeholder, ...props }) => {
    const [dataSource, setDataSource] = useState([]);
    const [filterDefaultValues, setFilterDefaultValues] = useState();
    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    })
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    })

    const getData = (search) => {
        if (reference) {
            const service = getService(reference);
            setLoading(true);
            return service.find({
                query: {
                    ...search,
                    ...filterDefaultValues,
                    $limit: 10
                }
            })
                .then(({ data }) => {
                    setLoading(false);
                    setDataSource(data);
                    /* setOpen(true); */
                })
                .catch(err => {
                    message.error(err.message)
                    setLoading(false);
                })
        }
    }
    const renderOption = item => {
        let image = item.gallery ? item.gallery.gallery_media && item.gallery.gallery_media[0] &&
            item.gallery.gallery_media[0].path ?
            `${URL_S3}/${item.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
            : DEFAULT_IMAGE;
        return (
            <Option key={item.id} text={item.id}>
                <div className="search-item">
                    <Link to={`/pack/${item.id}`}>
                        <div className="section-avatar-text"><Avatar shape={"square"} src={image} /><span className="Styles-text">{item[source]}</span></div>
                    </Link>
                </div>
            </Option>
        );
    }
    const handleSearch = value => {
        getData({
            ["q"]: value ? value : undefined
        });
    }
    const handleOnSelect = (item) => {
        console.log(item);
    }
    const handleChangeCity = (id) => {
        setFilterDefaultValues({
            ...filterDefaultValues,
            "city_id": id
        })
    }
    const handleOnSearch = _.debounce(handleSearch, 400, { maxWait: 1000 });
    useEffect(() => {
        if (filterDefaultValues)
            getData();
    }, [filterDefaultValues]);
    useEffect(() => {
        if (props.filterDefaultValues)
            setFilterDefaultValues(props.filterDefaultValues);
    }, [props.filterDefaultValues]);
    return (
        <Wrapper configuration={{
            primary_color: entity && entity.primary_color || "var(--color-primary);",
            secundar_color: entity && entity.secundar_color || "var(--color-secundary)",
            text_color: entity && entity.text_color || "var( --color-gray-dark)"
        }}>
            <AutoComplete
                /* open={open} */
                loading={loading}
                className="certain-category-search"
                dropdownClassName="certain-category-search-dropdown"
                dropdownMatchSelectWidth={false}
                dropdownStyle={{ minWidth: 300 }}
                size="large"
                style={{ width: '100%' }}
                prefix="close"
                dataSource={dataSource.map(renderOption)}
                onSearch={handleOnSearch}
                onSelect={handleOnSelect}
                placeholder={placeholder}
                optionLabelProp="value"
            >
                <Input prefix={<Icon type={!loading ? "search" : "loading"} className="certain-category-icon" />} />
            </AutoComplete>
            <DropDownCity
                onChange={handleChangeCity}
                t={t} />
        </Wrapper>
    )
}
export default translate()(SearchField);