import styled from 'styled-components';

export const Wrapper = styled.div`
    /* styles del search */
    & .section-container-search{
        padding-top:10px;
    }
    & .section-title-form h2{
        font-family: 'Jost'; 
        font-weight: 600;
        letter-spacing: 6.4px;
        color: #000000;
        text-transform: uppercase;
        opacity: 1;
    } 
    @media(max-width:576px){
        .section-container {
            padding: 0px!important;
        }
        .container-section{
            padding-top:24px!important;
        }
        .container-list {
            width: 100%!important;
        }
    }
        /*responsi lg  */
        @media (min-width: 992px){
            .ant-col-lg-24 {
                width: 50%;
                max-width: 48%;
    }                                         
    }      
        /* responsi md */
        @media (min-width: 768px){
        .ant-col-md-24 {
            width: 64%;
            max-width: 50%;
        }
    }

    & .section-title-contact h2{
        letter-spacing: 1.4px;
        color: #000000;
        text-transform: uppercase;
        opacity: 1;
        font-family: 'Jost';
        font-weight: 600;
    }
    & .section-container{
        padding:70px;
    }
    & .container-list{
        width: auto;
    }
`; 
export const WrapperList =styled.div`
& .container-avatar{

        background: #be5cff38;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 45px;
        height: 45px;
    
}
& .ant-avatar > img {
   
    width: max-content;
    height: max-content
}
& .ant-list-item-meta-content {
    display: flex;
    justify-content: flex-start;
    padding-top: 10px;
}
& .ant-list-split .ant-list-item {
    border-bottom: 1px solid #f4eded00!important;
}
`;